.list-container{
    /* background-color: khaki; */
    width: 100%;
    height: 300px;

    display: grid;
    grid-template-rows: 0.6fr 2fr 0.6fr 2fr;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 20px;
    column-gap: 20px;
}

.skill-heading{
    grid-column: span 3;

    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
}

.skill-container{
    grid-column: span 1;
    
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}



.blob{
    position: relative;
    width: 200px;
    height: 200px;

    border-radius: 74% 26% 26% 74% / 61% 47% 53% 39%;

    background-color: var(--purple);
   

    display: flex;
    align-items: center;
    justify-content: center;

    animation-name: spin;
    animation-timing-function: ease-in-out;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}

.blob svg{
    width: 100px;
    color: var(--background-color);
    fill: var(--background-color);
    transform: rotate(360deg);

    position: absolute;
    bottom: 20%;
    left: 10%;

    animation-name: spin-rev;
    animation-timing-function: linear;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    /* animation-direction: alternate-reverse; */
}

.blob svg, .blob{
    z-index: -1;
}

@keyframes spin {
    0%{
        border-radius: 74% 26% 26% 74% / 61% 47% 53% 39%;
    }
    100%{
        border-radius: 53% 47% 59% 41% / 34% 71% 29% 66%;
    }
}

@keyframes spin-rev {
    0%{
        color: var(--white);
        transform: rotate(360deg);
    }
    100%{
        color: var(--purple);
        transform: rotate(0deg);
    }
}