:root{

  --lightgrey : #d2d2d2;
  --grey : #7e7e7e;
  --darkgrey: #4d4d4d;
  --white : rgb(221, 221, 221);
  --dark-black-violet: rgb(5, 1, 12);
  /* --russian-violet: #10002bff;
  --russian-violet-2: #240046ff; */
  --persian-indigo: #00a8be;
  --purple: #1c2bff;

  --background-color : var(--dark-black-violet);
  
  --highlight-text : var(--purple);
  --navbar-bar-color : var(--purple);
  --link-text-color : var(--grey);
  --navbar-width : 100vw;
  --navbar-height : 60px;

  --spine-back-color : var(--darkgrey);
  --spine-bar-color : var(--purple);

  --screen-width : var(--navbar-width);
  --screen-height : calc(100vh - 60px);
  --screen-sahdow : 0px 150px 78px -44px rgba(5,1,12,1) inset,
                    0px -150px 78px -44px rgba(5,1,12,1) inset;

  --text-1 : monospace;
  --text-2 : 'Architects Daughter', cursive;
}

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;

  font-family: monospace;
  color: var(--white);
}

span{
  color: var(--highlight-text);
}

html {
  scroll-behavior: smooth;
}

body{
  width: 100vw;

  min-height: 100vh;
  height: 100vh;
  max-height: fit-content;

  background-color: var(--background-color);
  overflow-x: hidden;
}

.content{
  margin-top: var(--navbar-height);
}

.screen-container{
  width : var(--navbar-width);
  height: var(--screen-height);

  display: flex;
  align-items: center;
  justify-content: flex-start;
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */
  * {
    scrollbar-width: none;
    scrollbar-color: var(--spine-bar-color) var(--background-color);;
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 2px;
  }

  *::-webkit-scrollbar-track {
    background: var(--background-color);
  }

  *::-webkit-scrollbar-thumb {
    background-color: var(--spine-bar-color);
    border-radius: 20px;
    border: 5px none var(--background-color);
  }

  @media only screen and (max-width: 1000px) {
    body{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 100vw;
      height: 100vh;

      background-color: var(--background-color);
    }

    body::after{
      content: "Please view in Lrger Screen";
      font-size: 3rem;
      padding: 10px 40px;
      background-color: var(--purple);
      border-radius: 100px;

      animation-name: warning;
      animation-duration: 5s;
      animation-timing-function: ease-in-out;
      animation-iteration-count: infinite;
    }

    @keyframes warning {
      0%{
        transform: translateY(1vh);
      }
      50%{
        transform: translateY(-1vh);
      }
      100%{
        transform: translateY(1vh);
      }
    }

    body *{
      display: none;
    }
  }