.portrait-container{
    box-shadow: 65px 0px 84px -35px rgba(5,1,12,1) inset,
    -70px 0px 30px -45px rgb(5 1 12) inset,
    0px -133px 78px -45px rgba(5,1,12,1) inset,
    0px 90px 78px -20px rgba(5,1,12,1) inset;
}

.portrait{
    position: relative;
    z-index: -10;
    height: calc(var(--screen-height) * 0.6);
    filter:saturate(1.3) 
           blur(0px);
}
