h1, h2, h3{
    font-family: var(--text-1);
    letter-spacing: 1.5px;
}

h1 span, h2 span, h3 span{
    font-family: inherit;
    font-size: inherit;
    color: var(--purple);
}

h1{
    font-size: 65px;
}

h2{
    font-size: 18px;
    font-family: var(--text-1);

    color: var(--darkgrey)
}

h3{
    font-size: 18px;
    font-family: var(--text-2);

    color: var(--darkgrey)
}

.title{
    margin-bottom: 20px;
}

.section-left{
    width: calc(20vw - 1.5px);
    min-height: 100%;

    padding-right: 40px;
    margin-right: 3px;
    display: flex;

    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
}

.section-right{
    width: calc(80vw - 1.5px);
    min-height: 100%;

    padding: 0px 40px;

    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
}


.t-white{
    color: var(--white);
}
