.work-item-container{
    width: 100%;
    height: 300px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.work-item-container:not(:first-child){
    margin-top: 40px;
}

.work-item-container .image-container{
    width: 270px;
    height: 270px;
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;
    
    border-radius: 50%;
    background-color: var(--purple);
}

.work-item-container .image-container img{
    width: 100%;
}


.work-item-container article{
    width: calc(100% - 400px);
    height: 100%;

    padding-left: 45px;

    /* background-color: red; */
}