.spine-container{
    position: fixed;
    width: var(--screen-width);
    height: var(--screen-height);

    bottom: 0px;
    left: 0px;

    z-index: -2;

    /* background-color: blue; */
}

.spine-container .spine-back{
    width: 3px;
    height: var(--screen-height);

    position: absolute;
    left: 20%;
    left: calc(20% - 1.5px);

    background-color: var(--spine-back-color);
}

.spine-container .spine-bar{
    width: 3px;
    height: calc(var(--screen-height)/2);
    
    position: absolute;
    bottom: 0px;
    left: calc(20% - 1.5px);
    
    background-color: var(--spine-bar-color);
}

.spine-container .circle{
    width: 20px;
    height: 20px;

    position: absolute;
    top: calc(50% - 10px);
    left: calc(20% - 10px);

    border-radius: 50%;

    background-color: var(--spine-bar-color);
}