.links{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.links a{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    text-decoration: none;
}

.links > *{
    position: relative;
    z-index: 10;
}

.links a svg{
    width: 40px;
    height: 40px;
    fill: var(--purple);

    transition: all 150ms ease-in-out;
}

.links a:hover svg{
    transform: scale(1.1);
    fill: var(--white);
}

.links > *:not(:first-child){
    margin-left: 40px;
}

.links .btn{
    height: 40px;
    border : none;
    font-size: 18px;
    
    border-radius: 20px;
    padding: 5px 20px;

}

.links .btn i, .links .btn{
    color: var(--background-color);
    font-weight: 600;
    background-color: var(--purple);
    transition: all 150ms ease-in-out;
}

.links .btn:hover, .links .btn:hover i{
    transform: scale(1.1);
    background-color: var(--white);
}