.navbar-container{
    width: 100%;
    height: 60px;
    
    position: fixed;
    left: 0px;
    top: 0px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 0px 35px;
    background-color: var(--background-color);
}

.navbar-container::after{
    content: "";
    position: fixed;
    bottom: 0px;
    left: 0px;
    z-index: 2;

    width: var(--screen-width);
    height: var(--screen-height);
    box-shadow: var(--screen-sahdow);
}
