.link-container{
    position: relative;
    height: 100%;

    display: flex;
    align-items: center;
}

.link-container a{
    padding: 5px 15px;
    
    text-decoration: none;
    font-size: 1.25rem;
    color: var(--link-text-color);

    transition-delay: 500ms;
    transition: all 500ms ease-in-out;
}

.link-container a::after{
    content: "()";
    color: var(--highlight-text);
    transition-delay: 500ms;
    transition: all 500ms ease-in-out;
}

.link-container a.active::after{
    color: var(--white);
}

.link-container a.active{
   color: var(--white)
}


.link-container .bar{
    position: absolute;
    height: 29px;
    width: 0px;

    left: 0px;
    background-color: var(--navbar-bar-color );
    z-index: -1;

    border-radius: 20px;
    transition: all 300ms ease-in-out;
}